button
{
  width: 60px;
  height: 60px;
  background-color: #C4C4C4;
  border: none;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

button:hover
{
  cursor: pointer;
  border: none;
  outline: none;
  background-color: #999999;
}

button:focus
{
  cursor: pointer;
  border: none;
  outline: none;
}

